const get = (target) => {
    return document.querySelector(target);
}

const root = get('#root');
const ul = document.createElement('ul');
const content = document.createElement('div');

const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';

const ajax = new XMLHttpRequest();

let store = {
    currentPage: 1,
}

let { currentPage } = store;

const getData = (url) => {
    ajax.open('GET', url, false);
    ajax.send();
    return JSON.parse(ajax.response);
}

const newsFeed = () => {
    const newsList = getData(NEWS_URL);
    const newsArr = [];
    let template = `
        <div>
            <h1>Hacker News</h1>
            <ul>
                {{__news_feed__}}
            </ul>
            <div>
                <a href="#/page/{{__prev_page__}}">prev</a>
                <a href="#/page/{{__next_page__}}">next</a>
            </div>
        </div>
    `;

    for (let i = (currentPage - 1) * 10; i < (currentPage) * 10; i++) {
        newsArr.push(`
        <li>
            <a href="#/show/${newsList[i].id}">
                ${newsList[i].title}(${newsList[i].comments_count})
            </a>
        </li>
        `);
    }
    template = template.replace("{{__news_feed__}}", newsArr.join(''));
    template = template.replace("{{__prev_page__}}", currentPage > 1 ? currentPage - 1 : 1);
    template = template.replace("{{__next_page__}}", currentPage < 3 ? currentPage + 1 : 3);
    root.innerHTML = template;
}
//Í∏Ä Î™©Î°ù

const newsContent = () => {
    const id = location.hash.substring(7);
    const newsDetail = getData(CONTENT_URL.replace('@id', id));

    root.innerHTML = `
        <h1>${newsDetail.title}</h1>
        <div>
            <a href=#/page/${currentPage}>Î™©Î°ùÏúºÎ°ú</a>
        </div>
        `;
}
//Í∏Ä ÎÇ¥Ïö©

const router = () => {
    const routePath = location.hash;
    if (routePath === '') {
        newsFeed();
    } else if (routePath.indexOf('#/page/') >= 0) {
        currentPage = Number(routePath.substring(7));
        newsFeed();
    } else {
        newsContent();
    }
}
//ÌôîÎ©¥ Ï≤òÎ¶¨Í∏∞

router();
window.addEventListener('hashchange', router);